          id_card: "510724199706232816", //
          wechat_name: "xiaoli", //
          type: "", //
          yaks: "", //
          slaughterhouse: "", //
          total_balance: "200", //
          freezing_balance: "300", //
          create_time: "" //
        },
        {
          name: "",
          head_img: require("../../image/u3036.png"), //
          tel: "18200000000",
          id_card: "510724199706232816", //
          wechat_name: "xiaoli", //
          type: "",
          yaks: "", //
          slaughterhouse: "", //
          total_balance: "200", //
          freezing_balance: "300", //
          create_time: "" //
        },
        {
          name: "",
          head_img: require("../../image/u3036.png"), //
          tel: "18200000000",
          id_card: "510724199706232816", //
          wechat_name: "xiaoli", //
          type: "",
          yaks: "", //
          slaughterhouse: "", //
          total_balance: "200", //
          freezing_balance: "300", //
          create_time: "" //
        },
