          name: "",
          head_img: require("../../image/u3036.png"), //
          tel: "18200000000",
          id_card: "510724199706232816", //
          wechat_name: "xiaoli", //
          type: "",
          yaks: "", //
          total_balance: "200", //
          freezing_balance: "300", //
          create_time: "" //
        }
      ], // 
      form: {
        name: "",
        type: "", //
        wechat_name: "", //
        account: "", //
        password: "", //
        tel: "", //
        yaks: "", //
        id_card: "", //
        total_balance: "", //
        frozen_balance: "" //
      },
      page: {
        //
        pnum: 10,
        //
        // pageTotal: 15,
        //1
        currentPage: 1
      }
    };
  },

  methods: {
    // -
    handleSizeChange(val) {
      this.page.pnum = val;
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    // 
    reset(index) {
      this.isreset = true;
    },
    // 
    addherdsman() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },

    // 
    search() {
      let data = this.formInline;
      console.log(data);
      getCustomerList(data).then(res => {
        console.log(res);
      });
    },
    // 
    close() {
      if (this.isadd) {
        alert("");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("");
        this.ismodify = false;
      }
      if (this.isreset) {
        alert("");
        this.isreset = false;
      }
    },
    // 
    modify(index, Modifyform) {
      this.ismodify = true;
      this.head_imgs = [{ url: Modifyform.head_img }];
      this.form = {
        name: Modifyform.name,
        tel: Modifyform.tel,
        id_card: Modifyform.id_card, //
        wechat_name: Modifyform.wechat_name, //
        type: Modifyform.type, //
        yaks: Modifyform.yaks, //
        total_balance: Modifyform.total_balance, //
        freezing_balance: Modifyform.freezing_balance //
      };
      console.log(Modifyform);
    },
    // banner
    deleted() {
      this.isdeleted = true;
    },
    // 
    really(data) {
      if (this.isadd) {
        alert("");
        this.isadd = false;
        let data = this.form;
        data.head_img = this.head_imgs[0];
        postCustomer(data).then(res => {
          console.log(res);
        });
      }
      if (this.ismodify) {
        alert("");
        this.ismodify = false;
        data.head_img = this.head_imgs[0];
        modifyCustomer(data).then(res => {
          console.log(res);
        });
      }
      if (this.isreset) {
        this.isreset = false;
        alert("");
      }
    },
    //
    cancel() {
      if (this.isadd) {
        alert("");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("");
        this.ismodify = false;
      }
      if (this.isreset) {
        alert("");
        this.isreset = false;
      }
    },
    //
    handleRemove(file, fileList) {},
    //
    handlePictureCardPreview(esponse, file, fileList) {
      this.head_imgs = file.url;
      this.head_imgs = this.head_imgs.slice(5);
      console.log(file.url);
    },
    //
    getGoodsList() {
      // console.log("aaa")
      getCustomerList().then(res => {
        console.log(res);
      });
    }
  },
  created() {
    this.getGoodsList();
  }
};
